.JumboGrove {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.JumboGrove .JGUI > article {
  max-width: 40rem;
  margin: auto;
}
.JumboGrove .JGUI > nav {
  padding: 0 1rem;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  right: calc(50% + 20rem);
}
.JumboGrove .JGUI > aside {
  padding: 0 1rem;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  left: calc(50% + 20rem);
}
.JumboGrove .JGUI > aside ul {
    list-style-type: none;
    padding-left: 0;
}
.JumboGrove .JGUI > aside ul ul ul {
      list-style-type: disc;
      list-style-position: inside;
}
.JGUIScrollSpacer {
  height: 2000px;
}
.title {
  text-align: center;
  border-top: 1px solid black;
  padding-top: 1rem;
  font-size: 2rem;
}
.character {
  font-style: normal;
  font-weight: bold;
}

.JGHTMLAddition, .JGHTMLItem {
  opacity: 0;
}
.m-inactive .JGHTMLAddition, .m-inactive .JGHTMLItem {
  opacity: 1;
}
.JGHTMLItem.m-inactive {
  opacity: 1;
}
.JGHTMLAddition.m-animated, .JGHTMLItem.m-animated {
  -webkit-animation: JGAdditionOpacity 1s;
          animation: JGAdditionOpacity 1s;
  opacity: 1;
}
@-webkit-keyframes JGAdditionOpacity {
0% { opacity: 0;
}
100% { opacity: 1;
}
}
@keyframes JGAdditionOpacity {
0% { opacity: 0;
}
100% { opacity: 1;
}
}

@charset "UTF-8";
.m-disabled-link.m-selected[data-v-8dbc74e8]::after {
  content: " \2714";
}
